<template>
  <div>
    <el-table
        :data="tableData"
        stripe
        ref="table"
        :height="height"
        style="width: 100%">
      <slot name="tableHead"></slot>
    </el-table>
    <el-pagination
        background
        ref="pagination"
        layout="sizes,prev, pager, next,jumper"
        :current-page="page"
        @current-change="pageChange"
        @size-change="countChange"
        :total="totalCount">
    </el-pagination>
  </div>
</template>

<script>
  export default {
    name: "myTable",
    data() {
      return {
        page: 1,
        count: 10,
        tableHeight: null
      }
    },
    props: ['totalCount', 'tableData', 'height'],
    created() {
      this.getTable()
    },
    methods: {
      refresh() {
        if (this.page < 1) this.page = 1
        this.getTable()
      },
      search() {
        this.page = 1
        this.count = 10
        this.getTable()
      },
      getTable() {
        this.$emit('getData', this.page, this.count)
      },
      computeIndex(index) {
        return (this.page - 1) * this.count + index + 1
      },
      pageChange(page) {
        this.page = page
        this.getTable()
      },
      countChange(count) {
        this.count = count
        this.page = 1
        this.getTable()
      },
    }
  }
</script>

