import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from './network/index.js'
import myLayout from "./components/common/myLayout.vue";
import myTable from "./components/common/myTable.vue";
// import elementUi from 'element-ui'
import {
  Table,
  TableColumn,
  MessageBox,
  Message,
  Form,
  FormItem,
  Input,
  Button,
  Card,
  Loading,
  Pagination,
  RadioGroup,
  Radio,
  Dropdown,
  DropdownItem,
  DropdownMenu,
  Avatar,
  Select,
  Option
} from "element-ui"
import 'element-ui/lib/theme-chalk/index.css'

Vue.prototype.axios = axios
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Input)
Vue.use(Button)
Vue.use(Card)
Vue.use(Pagination)
Vue.use(RadioGroup)
Vue.use(Radio)
Vue.use(Dropdown)
Vue.use(DropdownItem)
Vue.use(DropdownMenu)
Vue.use(Avatar)
Vue.use(Select)
Vue.use(Option)
Vue.prototype.$prompt = MessageBox.prompt
Vue.prototype.$message = Message
Vue.prototype.$msgbox = MessageBox;
Vue.prototype.$alert = MessageBox.alert;
Vue.prototype.$confirm = MessageBox.confirm;
// Vue.prototype.$loading = Loading.service;

Vue.component('myLayout', myLayout)
Vue.component('myTable', myTable)

Vue.prototype.loading = function () {
  this.__proto__.loaded = Loading.service({/*Vue.prototype.loaded*/
    lock: true,
    text: '...',
    background: 'rgba(0, 0, 0, 0.7)'
  })
}
Vue.prototype.$err = function (err, callback) {
  console.log(err)
  this.$message.error(',')
  if (this.loaded) this.loaded.close()
  callback && callback()
}
Vue.prototype.getTableData = function (url, params, callback) {
  this.loading()
  this.tableData.length = 0
  // if (!params) {
  //   params = {}
  //   params.page = this.page
  //   params.count = this.count
  // }
  if (!params) params = {}
  Object.assign(params, {page: this.page, count: this.count})
  // if (!params.page) params.page = this.page
  // if (!params.count) params.count = this.count
  // this.page = params.page//
  // let fullUrl = url + '?'
  // for (let k in params) {
  //   fullUrl += k + '=' + params[k] + '&'
  // }
  // this.axios.get(fullUrl.substr(0, fullUrl.length - 1)).then(r => {
  this.axios.get(url, {params}).then(r => {
    if (!r || !r.data) {
      this.loaded && this.loaded.close()
      return this.$message.error('')
    }
    if (r.data.notLogin) {
      this.loaded.close()
      return false
    }
    let data = r.data.data
    this.tableData.push(...data[0])
    this.totalCount = data[1].totalCount
    callback && callback(data)
    this.loaded.close()
    return data
  }, err => this.$err(err))
}

Vue.config.productionTip = false
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
