<template>
  <div>
    <slot name="form"></slot>
    <slot name="tableLayout" :height="tableHeight"></slot>
  </div>
</template>

<script>

  export default {
    name: 'myLayout',
    data() {
      return {
        searchForm: {
          courseName: '',
          teacherName: '',
          classroom: ''
        },
        ruleFormIndex: 'ruleForm' + Math.ceil(Math.random() * 100),
        tableData: [],
        totalCount: 0,
        selectedCourseId: '',
        page: 1,
        count: 10,
        tableHeight: null
      }
    },
    created() {
    },
    mounted() {
      let slots = this.$slots
      //   //
      if (slots['form']) {
        let form = this.$slots.form[0].child
        let inputWidth = form.$children[0].$el.clientWidth + 10 // +margin
        let inputCount = form.$children.length - 1
        let formWidth = form.$el.clientWidth
        let btn = form.$children[inputCount].$el
        let lineNum = formWidth >= inputWidth * 3 ? 3 : 2 //
        let lastLineNum = inputCount % lineNum //
        btn.style.marginLeft = (lineNum - lastLineNum - 1) * inputWidth + 100 + 'px' // 100 label
      }
      //
      let winHeight = document.documentElement.clientHeight
      let headerHeight = 50
      let formHeigth = slots['form'] ? slots['form'][0].child.$el.clientHeight : 0
      // let paginHeight = slots.pagination[0].child.$el.clientHeight
      // this.tableHeight = winHeight - headerHeight - formHeigth - paginHeight - 48 + 'px'
      this.tableHeight = winHeight - headerHeight - formHeigth - 80 + 'px'
    },
    methods: {},
  }
</script>
<style lang="less">
  .search_form {
    /*width: 35vw;*/

    .el-form-item {
      /*width: 25vw;*/
      min-width: 300px;

      .el-form-item__content {
        /*width: 15vw;*/
        width: 200px;

        .el-input, .el-select {
          width: 100%;
        }
      }

      .el-form-item__label {
        font-size: 13px;
        /*width: 10vw;*/
        width: 100px;
      }
    }

    .el-form-item:last-of-type { /*.search_btn*/
      /*width: 15vw;*/
      min-width: 200px;
    }
  }

  .el-pagination {
    text-align: right;
    margin-top: 16px;
  }
</style>
